.MainPage {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;
    overflow: auto;
    background-color: cornflowerblue;
  
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
  }
  .ChatContainer{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100vh;
  }

  @media(max-width: 719px){  
    .MainPage{
      min-height: unset;
      height: 100%;
    }
    .ChatContainer{
      height: 100%;
      width: 200vw;
      flex-direction: row-reverse;
    }
  }