.InfoPage{
    width: 100%;
    height: 100vh;
    overflow: auto;
    background-color: cornflowerblue;
}

.Searcher{
    position: relative;
    width: 95%;
    height: 60vh;
    min-height: 300px;
    max-width: 700px;
    margin: 20px auto;

    /* border: 1px solid black; */
    border-radius: 20px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    box-shadow: 3px 3px 10px rgba(0,0,0,.3);
    background-color: white;
}

.Navigator{
    width: 100%;
    flex: 0 0 50px;

    display: flex;
    flex-direction: row;
    border-bottom: 2px solid var(--darker-color);
    box-shadow: 0 3px 5px var(--transparent-darker-color);
    z-index: 1;
}
.NavButtons{
    height: 100%;
    flex: 0 0 50px;
    display: flex;
    flex-direction: row;
    border-right: 1px solid rgba(255, 255, 255, 0.5);
}
.NavButtons button{
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--violet-color);
    border: none;
    transition: background-color .2s, box-shadow .2s;
    outline: none;
    border-radius: 0;
}
.NavButtons button:hover{
    background-color: var(--darker-color);
}
.NavButtons button:focus{
    border: 1px solid var(--darker-color);
}
.NavButtons button:active{
    box-shadow: inset 0 0 5px rgba(0,0,0,.5);
}
.NavButtons button svg{
    width: 50%;
    height: 50%;
    color: white;
    transform: rotateZ(65deg);
}

.NavLinks{
    height: 100%;
    flex-grow: 1;

    display: flex;
    flex-direction: row;
}
.NavLinks a{
    flex-grow: 1;
    height: 100%;
    color: white;
    background-color: var(--violet-color);
    text-decoration: none;
    transition: background .3s;
    outline: none;

    display: flex;
    justify-content: center;
    align-items: center;
}
.NavLinks a:hover,
.NavLinks a.Active{
    background-color: var(--darker-color);
}
.NavLinks a:focus{
    border: 1px solid var(--darker-color);
}

.IFrame{
    width: 100%;
    flex-grow: 1;
}
.IFrameBg{
    width: 100%;
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    padding: 10px;
    text-align: center;
    color: white;
    background-color: var(--secondary-color);
}

.LinkOtherTab{
    position: absolute;
    bottom: 10px;
    right: 10px;
    padding: 8px;
    padding-bottom: 5px;
    background-color: var(--secondary-color);
    border-radius: 10px;
    box-shadow: 0 0 8px rgba(0,0,0,.3);
}
.LinkOtherTab svg{
    color: white;
    width: 30px;
    height: 30px;
}
