.ChatDescription{
    width: 55%;
    min-height: 100%;
    overflow-y: auto;
    
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;

    background-color: var(--main-color);
    background-image: url("../../assets/pattern3.png");
    background-repeat: repeat;
    box-shadow: -2px 0px 5px rgba(0,0,0,.5);
}

.ChatDescription section{
    width: 95%;
    max-width: 500px;
    padding: 15px 0px;

    color: white;
}
.ChatDescription section p{
    font-size: 1.4rem;
    margin: 10px 0;
}
.ChatDescription section hr{
    margin-top: 50px;
    margin-bottom: 30px;
    border: 1px solid rgba(255, 255, 255, 0.7);
    position: relative;
    overflow: visible;
}
.ChatDescription section hr:after{
    content: "O";
    display: inline-block;
    position: absolute;
    left: 50%;
    top: -0.7em;
    color: rgba(255, 255, 255, 0.7);
    background-color: var(--main-color);
}

.ButtonGoChat{
    display: none;
}

@media(max-width: 719px){
    .ChatDescription{
        width: 50%;
        scroll-snap-align: center;
    }

    .ButtonGoChat{
        width: 80%;
        height: 80px;
        margin: 0 auto;
        margin-bottom: 20px;
        background-color: var(--secondary-color);
        border-radius: 20px;
        border: none;
        box-shadow: 0 2px 10px rgba(0,0,0,.3), inset 0 0 0 transparent;
    
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    
        -webkit-tap-highlight-color: transparent;
        outline: none;
        cursor: pointer;
        transition: box-shadow .1s;

        padding-left: 35px;
    }
    .ButtonGoChat:active{
        box-shadow: 0 0 0 transparent, inset 0 0 5px rgba(0,0,0,.5);
    }
    .ButtonGoChat p,
    .ButtonGoChat svg{
        color: white;
        font-size: 1.6rem;
        font-family: 'Open Sans', sans-serif;
        margin: 0px 10px;
        font-weight: bold;
    }
    .ButtonGoChat svg{
        height: 50px;
        width: 50px;
    }
}