.HintsContainer{
    flex: 0 0 60px;
    padding-top: 5px;
    width: 100%;
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
    list-style: none;

    /* background-color: rgba(0,0,0,.1); */
    background-color: #6ec47e2a;
    border-top: 1px solid var(--secondary-color);
}
.HintsContainer button{
    display: inline-block;
    padding: 5px 10px;
    margin: 5px 10px;
    background-color: var(--secondary-color);
    color: white;
    font-size: 1em;
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 1px 1px 5px rgba(0,0,0,.2);
    -webkit-tap-highlight-color: transparent;
    outline: none;
    border: 0;
    transition: background .2s, box-shadow .2s;
}
.HintsContainer button:hover,
.HintsContainer button:focus{
    background-color: var(--darker-secondary-color);
}
.HintsContainer button:active{
    box-shadow: 0 0 0 transparent;
}


.ChatInput{
    flex: 0 0 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.ChatInput input{
    flex-grow: 1;
    height: 100%;
    min-width: 200px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    border: none;
    border-top: 1px solid grey;
    padding: 0 5px;

    font-size: 1.2rem;
    caret-color: var(--violet-color);
}
.ChatInput button{
    flex: 0 0 100px;
    height: 100%;
    background-color: var(--main-color);
    color: white;
    font-size: 1.2rem;
    border: none;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: background .3s;
    cursor: pointer;
}
.ChatInput button:hover,
.ChatInput button:active{
    background-color: var(--darker-color);
}