.ChatInterface{
    width: 45%;
    height: 100%;
    overflow-y: auto;
    background-color: #fdfdfd;
    display: flex;
    flex-direction: column;
}

.DialogBox{
    flex-grow: 1;

    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;

    padding: 15px;
    box-sizing: border-box;
}

.ButtonReturn{
    display: none;
}

@media(max-width: 719px){
    .ChatInterface{
        width: 50%;
        scroll-snap-align: center;
    }

    .ButtonReturn{
        width: 100%;
        flex: 0 0 50px;
        background-color: var(--darker-color);
        border: 1px solid whitesmoke;
    
    
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
    .ButtonReturn p,
    .ButtonReturn svg{
        color: white;
        font-size: 1.6rem;
        font-family: 'Open Sans', sans-serif;
        margin: 0px 10px;
    }
    .ButtonReturn svg{
        width: 40px;
        height: 40px;
    }
}